import React from "react";
import { useBasket } from "./basketContext";

const Card = ({ id, title, url, desc, price }) => {
  const { addToBasket } = useBasket();

  const handleAddToBasket = () => {
    addToBasket({ id, title, url, desc, price, quantity: 1 });
  };

  return (
    <div className="card">
      <div
        className="card-header"
        style={{ backgroundColor: "white", padding: "0", border: "0" }}
      >
        <img
          src={url}
          alt="photo of dish"
          className="img-responsive rounded-2 w-100"
        />
      </div>
      <div className="card-body">
        <div className="d-flex justify-content-between align-items-center mb-3">
          <h4 className="title mb-0 ">{title}</h4>
          <p className="price mb-0">${price}</p>
        </div>
        <p>{desc}</p>
      </div>
      <div className="footer-card d-flex justify-content-between ">
        <div>
          Order Online <span>&nbsp;</span>
          <svg
            xmlns="http://www.w3.org/2000/svg"
            width="30"
            height="30"
            fill="currentColor"
            className="bi bi-bicycle"
            viewBox="0 0 20 20"
          >
            <path d="M4 4.5a.5.5 0 0 1 .5-.5H6a.5.5 0 0 1 0 1v.5h4.14l.386-1.158A.5.5 0 0 1 11 4h1a.5.5 0 0 1 0 1h-.64l-.311.935.807 1.29a3 3 0 1 1-.848.53l-.508-.812-2.076 3.322A.5.5 0 0 1 8 10.5H5.959a3 3 0 1 1-1.815-3.274L5 5.856V5h-.5a.5.5 0 0 1-.5-.5m1.5 2.443-.508.814c.5.444.85 1.054.967 1.743h1.139zM8 9.057 9.598 6.5H6.402zM4.937 9.5a2 2 0 0 0-.487-.877l-.548.877zM3.603 8.092A2 2 0 1 0 4.937 10.5H3a.5.5 0 0 1-.424-.765zm7.947.53a2 2 0 1 0 .848-.53l1.026 1.643a.5.5 0 1 1-.848.53z" />
          </svg>
        </div>
        <button
          className="btn-add"
          onClick={handleAddToBasket}
          style={{
            backgroundColor: "transparent",
            border: "none",
            color: "#495e57",
            fontSize: "1.1rem",
          }}
        >
          Add <span>&nbsp;</span>
          <i className="bi bi-bag-check-fill"></i>
        </button>
      </div>
    </div>
  );
};

export default Card;
